/**
 * Any shared variables between your bundles.
 */

$grid-gutter-width: 40px !default;

$sm-screen: 576px;
$md-screen: 768px;
$lg-screen: 992px;
$xl-screen: 1200px;

$font-primary: 'knockout', Helvetica, Arial, sans-serif;
$font-secondary: 'Open Sans', Helvetica, Arial, sans-serif;
$font-bold: 700;
$font-semi-bold: 600;
$font-regular: 400;

$black: #000;
$white: #fafcfe;

$theme-black: $black;
$theme-white: $white;

$error  : #ef3c00;
$warning: #ffcc01;
$success: #31d0aa;

$box-shadow: 4px 4px 5px -2px rgba(0, 0, 0, .2);

$primary-color: #de6a52;
$primary-colors: (
  '40': $primary-color,
  '30': #e3836e,
  '20': #ecab9e,
  '10': #f5d5ce
);

$secondary-color: #3089c4;
$secondary-colors: (
  '40': $secondary-color,
  '30': #7eb5da,
  '20': #c5ddee,
  '10': #f0f6fb
);

$text-color: #322520;
$text-colors: (
  '40': $text-color,
  '30': #544847,
  '20': #8c8483,
  '10': #c6c2c1
);

$grey: #fafbfc;
$greys: (
  '40': $grey,
  '30': #f1f5f7,
  '20': #f5f7f9,
  '10': #fafbfc
);

$dark: #271e1d;
$darks: (
  '40': $dark,
  '30': #4a4442,
  '20': #868180,
  '10': #c2c0c0
);

